.info-section {
      width: 100%;
      height: 100%;
}

.header-image-container {
    position: relative;
}
  
.small-header-image {
      width: 100%;
      height: 80px;
      object-fit: cover;
      object-position: 100% 35%;
}
  
 .page-info {
      position: absolute;
      left: 20px;
      top: 5px;
      font-size: 1.3em;
      color: white;
      text-align: left;
      font-family: 'Segoe UI';
}
  
.page-info span {
      font-weight: bold;
}

.back-container {
      display: flex;
      align-items: center;
      margin-top: 40px;
      margin-left: 50px;
      position: absolute;
      text-decoration: none;
      color: black;
}

.back-container img {
      max-width: 30px;
      
}

.back-container p {
      font-weight: bold;
      font-size: 1.2em;
      margin-left: 10px;
}


.dog-info-container {
      margin-top: 50px;
      background-color: white;
      border-radius: 10px;
      width: 900px;
      height: 500px;
      margin-left: auto;
      margin-right: auto;
      display: flex;
      border: 1px solid gray;
}

.dog-info-container img {
      margin: 30px;
      max-width: 400px;
      object-fit: cover;
      object-position: 40% 0;
      border: 1px solid black;
      border-radius: 10px;
     
}

.info-text-container {
      margin-top: 20px;
      text-align: left;
      font-size: 1.3rem;
}

.bold {
      font-weight: bold;
}

@media only screen and (max-width: 768px) {
      .dog-info-container {
            width: 80%;
            max-width: 400px;
            height:auto;
            flex-direction: column;
      }

      .info-text-container {
            margin-top: 0%;
            text-align: left;
            padding-left: 25px;
            font-size: 1rem;
      }

      .back-container {
            
            margin-top: 0px;
            margin-left: 20px;
            position: absolute;
            color: rgb(0, 0, 0);
      }

      .back-container img {
            max-width: 15px;
            
      }
      
      .back-container p {
            font-weight: bold;
            font-size: 1em;
            margin-left: 10px;
      }
}